<template>
	<div id="app" style = 'display: none'>
        <h1 class = 'title'>POSTO DE <span>GASOLINA</span></h1>
        <h2 >ABASTECIMENTO DE COMBUSTÍVEL.</h2>

        <div class = 'datas-container'>
            <div class="data">
                <h1>TANQUE</h1>
                <p>{{tank}}</p>
            </div>
            <div class="data">
                <h1>VALOR</h1>
                <p>{{price}}</p>
            </div>
            <div class="data">
                <h1>LITRO</h1>
                <p>{{lts}}</p>
            </div>
        </div>
	</div>
</template>

<script>
export default {
    components: { 
        
    },

    data() {
        return {
            price: '0',
            lts: '0',
            tank: '0'
        }
    },


    mounted() {
		this.listener = window.addEventListener('message', ({data}) => {
            if (data.show == true) {
                this.JQ('#app').fadeIn(500)
            } else if (data.show == false) {
                this.JQ('#app').fadeOut(500)
            }

            if (data.tank !== undefined) {
                this.price = "$" + data.price
                this.lts = data.lts + " ¢"
                this.tank = data.tank + " %"
            }
        }, false);


        this.listener = window.addEventListener('keyup', ({keyCode}) => {
            if (keyCode == '27') {
                this.JQ('#app').fadeOut(500)

                let options = {
                    method: 'POST',
                    body: JSON.stringify({})
                }

                fetch(this.GLOBAL.url + '/closeSystem', options)
            } 
        }, false);
	}
}
</script>

<style scoped>
#app {
    position: absolute;
    left: 40px;
    bottom: 40px;
    width: 300px;
    display: flex;
    flex-direction: column;

    background: rgba(17, 17, 17, 0.8);
    border-radius: 10px;
    padding: 20px;
}

.title {
    font-family: 'Montserrat';
    font-size: 18px;
    color: white;
}

.title span {
    color: #d8eb84;
}

h2 {
    font-family: 'Montserrat';
    font-size: 10px;
    color: rgba(255, 255, 255, 0.5);
    font-weight: 400;
}

/* ==================================================================================== */


.datas-container {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 20px;
    grid-gap: 10px;
}

.data {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: rgba(0, 0, 0, 0.19);
    border-radius: 6px;
    padding: 10px;
    
}

.data h1 {
    font-family: 'Montserrat';
    font-size: 13px;
    color: white;
}

.data p {
     font-family: 'Montserrat';
    font-size: 12px;
    color: rgba(255, 255, 255, 0.5);
    font-weight: 400;
    margin-top: 4px;
}

</style>

